          pageno:1,
          pagesize:4,
          current_sort_key:0,
          current_rack_key:null,
          totalpage:[],
          goodsItem:[]
        }
      },
      components:{
        Swiper,Search,Selector, Group,Actionsheet,Scroller,Loading,marketList
      },
      mounted(){
        setTitle("跳蚤市场首页");
        this.getlist();
