           for(let i=0;i<arr_sort.length;i++)
           {
           json_sort[arr_sort[i].key]=arr_sort[i].value
           }
           self.sort=json_sort;

           let arr_rack=data.body.data.fields.rack;
           let json_rack={};
           for(let j=0;j<arr_rack.length;j++)
